$width-tablet: 540px;
$width-pc: 900px;


.title {
  width: calc(100% - 32px);
  max-width: 1248px;
  margin: 0 auto;

  h2 {
    font-size: 24px;
    font-weight: bold;
    margin-top: 64px;
  }

  h5 {
    margin-top: 4px;
    margin-bottom: 32px;
    font-weight: normal;
  }
}

.works {
  display: flex;
  flex-wrap: wrap;
  width: calc(100% - 16px);
  max-width: 1280px;
  margin: 0 auto;

  section {
    background-color: #fafafa;
    border-radius: 4px;
    border: 1px solid #fff;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    margin: 0 8px 16px 8px;
    width: 100%;

    @media screen and (min-width: $width-tablet) {
      width: calc((100% / 2) - 16px);
    }

    @media screen and (min-width: $width-pc) {
      width: calc((100% / 3) - 16px);
    }

    .imageWrapper {
      position: relative;
      width: 100%;
      border: 2px solid #fefefe;
      border-radius: 2px;

      &:before {
        content:"";
        display: block;
        padding-top: 52.5%;
      }

      .image {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        border-radius: 2px;
        background-color: #aaa;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
      }
    }

    .info {
      padding: 16px;

      h3 {
        font-size: 14px;
        line-height: 20px;
        height: 40px;
        margin-bottom: 8px;
      }

      p {
        font-size: 14px;
        height: 28px;

        a {
          font-size: 14px;
          color: #369;
        }
      }

      .date {
        font-size: 14px;
        color: #aaa;
      }
    }
  }
}
