$width-tablet: 540px;
$width-pc: 900px;

.section {
  background-color: #fafafa;
  border-radius: 4px;
  border: 1px solid #fff;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
  margin: 16px auto;
  padding: 16px;
  width: calc(100% - 32px);
  max-width: 720px;

  h3 {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 8px;
  }

  article {
    display: flex;
    flex-wrap: wrap;
    margin-top: 16px;

    div {
      $date-width: 72px;
      line-height: 23px;

      &:nth-child(1) {
        width: 100%;
        color: #999;

        @media screen and (min-width: $width-tablet) {
          width: #{$date-width};
        }
      }

      &:nth-child(2) {
        width: 100%;

        @media screen and (min-width: $width-tablet) {
          width: calc(100% - #{$date-width});
        }
      }

      a {
        color: #369;
      }
    }
  }
}
