
.hero {
  width: 100%;
  height: 200px;
  background-color: #666;
  background-image: url("../../public/img/background.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  border-bottom: 2px solid #fefefe;

  &::before{
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0,0,0,0.7);
  }
}

.icon {
  $r: 108px;
  width: $r;
  height: $r;
  bottom: -0.5 * $r;
  left: calc(50% - (0.5 * #{$r}));
  border-radius: 50%;
  background-color: #0f0;
  position: absolute;
  border: 2px solid #fefefe;
  background-image: url("../../public/img/icon_rect.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

h1 {
  font-size: 28px;
  color: #333;
  text-align: center;
  margin: 72px 0 0 0;
}

h2 {
  font-size: 20px;
  color: #aaa;
  text-align: center;
  margin: 0 0 0 0;
}

h5 {
  font-size: 16px;
  font-weight: normal;
  color: #333;
  text-align: center;
  margin: 16px 0 0 0;
}

.box {
  margin: 64px auto 16px auto;
  max-width: 720px;
  width: calc(100% - 32px);

  p {
    line-height: 32px;
  }

  .links {
    width: fit-content;
    margin: 32px auto;

    a {
      display: inline-block;
      $r: 48px;
      width: $r;
      height: $r;
      border-radius: 50%;
      background-color: #fefefe;
      border: 1px solid #fefefe;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center center;

      &.icon_tw {
        background-image: url("../../public/img/icon-twitter.svg");
      }

      &.icon_gh {
        background-image: url("../../public/img/icon-github.svg");
      }

      &.icon_nt {
        background-image: url("../../public/img/icon-note.svg");
        background-position: center center;
      }
    }

    span {
      color: #eee;
      margin: 0 16px;
    }
  }
}
